@import url(https://fonts.googleapis.com/css2?family=Concert+One&display=swap);
body {
  font-family: "Concert One", cursive;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 1rem;
  padding: 1rem;
  background-color: #fef6e4 !important;
}

h1 {
  color: hotpink;
  padding: 2rem;
}

h3 {
  color: #8bd3dd;
  padding: 1rem;
}

.content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 2rem 0;
}

footer {
  color: #b8c1ec;
  padding: 3rem;
}

footer a {
  text-decoration: none;
  color: hotpink;
}

